}

.btn {
  width: 250px;
  height: 45px;
  font-size: 14px;
  background-color: #fac577;
  border: transparent;
  border-radius: 50px;
  cursor: pointer;
}

.btn:hover,
.btn:active,
.btn:focus {
  background-color: #fff;
}

.btn--blue {
  color: #fff;
  background-color: #2961ad;
}

.btn--blue:hover,
.btn--blue:active,
.btn--blue:focus {
  color: #e6e6e6;
  background-color: #1f4a84;
}

h1.title,
h2.title {
  font-weight: 700;
  font-size: 36px;
  line-height: 1.3;
  letter-spacing: 0.02em;
  text-transform: uppercase;
}

h2.title {
  font-weight: 400;
  font-size: 35px;
}

h3.title {
  font-weight: 400;
  font-size: 25px;
  line-height: 1.3;
  text-align: center;
}

h4.title {
  font-weight: 400;
  font-size: 20px;
  line-height: 1.05;
}

.advantage {
