}

.advantage__title {
  margin-bottom: 100px;
  text-align: center;
}

.advantage__plus {
  max-width: 250px;
  text-align: center;
}

.advantage__plus strong {
  display: block;
  font-weight: 400;
  font-size: 60px;
  line-height: 1;
}

.advantage__plus span {
  font-size: 14px;
}

.contacts {
  background-color: #2961ad;
  color: #fff;
  display: flex;
  justify-content: flex-end;
}

.contacts .container {
  flex: 1 1 50%;
  max-width: 660px;
  margin: 0;
  padding: 35px 15px 90px;
}

.contacts .container .header__top {
  flex-direction: column;
  align-items: flex-start;
  border-bottom: none;
}

.contacts .container .header__top > div {
  display: flex;
  align-items: center;
}

.contacts .container .header__top > div p {
  font-family: "PT Sans", sans-serif;
}

.contacts .container .header__top .btn {
  width: 150px;
  margin-left: 40px;
}

.contacts__title {
  text-align-last: left;
  margin-bottom: 40px;
}

.contacts__phone {
  margin-bottom: 1.5em;
}

.contacts__marker {
  margin-bottom: 1.5em;
}

.contacts__map {
  width: 50%;
}

.contacts__map [class*="ymaps-2"][class*="-ground-pane"] {
  filter: url("data:image/svg+xml;utf8,<svg xmlns='http://www.w3.org/2000/svg'><filter id='grayscale'><feColorMatrix type='matrix' values='0.3333 0.3333 0.3333 0 0 0.3333 0.3333 0.3333 0 0 0.3333 0.3333 0.3333 0 0 0 0 0 1 0'/></filter></svg>#grayscale");
}

.cta {
  position: relative;
  z-index: 0;
  padding: 34px 0;
  color: #fff;
  background-color: #e5e5e5;
}

.cta::before {
  content: "";
  position: absolute;
  z-index: -1;
  top: -1px;
  left: 0;
  width: 100%;
  height: 111px;
  background-color: #fff;
}

.cta .container {
  position: relative;
  max-width: 1290px;
  padding: 95px 0 90px;
  background: linear-gradient(rgba(64, 64, 64, 0.5)), url("../img/cta/bg.webp"), #c4c4c4;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}

.cta .free .btn {
  flex-grow: 1;
  max-width: 250px;
}

.cta .free__form {
  flex-direction: row;
  justify-content: center;
  margin-bottom: 27px;
}

.cta .free__input {
  flex-grow: 1;
  max-width: 250px;
  margin-right: 20px;
}

.cta .free__span {
  display: block;
  text-align: center;
}

.cta .free__span a {
  color: #fff;
  text-decoration: underline;
}

.cta__corner {
  display: block;
  position: absolute;
  top: -34px;
  left: -26px;
  width: 520px;
  height: 13px;
  background-color: #e5e5e5;
}

.cta__corner::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 13px;
  height: 53px;
  background-color: #e5e5e5;
}

.cta__corner--right {
  top: unset;
  right: -26px;
  bottom: -34px;
  left: unset;
  background-color: #fff;
}

.cta__corner--right::before {
  top: unset;
  right: 0;
  bottom: 0;
  left: unset;
  width: 13px;
  height: 53px;
  background-color: #fff;
}

.cta__title {
  max-width: 600px;
  margin: 0 auto 35px;
  text-align: center;
}

.faq {
  position: relative;
  z-index: 0;
  padding: 56px 0 40px;
}

.faq .btn {
  display: block;
  margin: 0 auto;
}

.faq__title {
  margin-bottom: 100px;
}

.faq__list {
  margin-bottom: 40px;
  padding-left: 11px;
}

.faq__item {
  position: relative;
  border: 2px solid rgba(41, 97, 173, 0.5);
  padding: 16px 70px;
  margin-bottom: 8px;
  cursor: pointer;
}

.faq__item i {
  position: absolute;
  left: -12px;
  width: 23px;
  height: 23px;
  background-color: #2961ad;
  border-radius: 50%;
}

.faq__item i::before,
.faq__item i::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  width: 15px;
  height: 2px;
  transform: translate(-50%, -50%);
  background-color: #fff;
  border-radius: 2px;
}

.faq__item i::after {
  width: 2px;
  height: 15px;
  transition: .4s;
}

.faq__item i.active::after {
  height: 0;
}

.faq__item h4 {
  margin-bottom: 1em;
}

.faq__item p {
  font-size: 14px;
  display: none;
}

.free {
  display: flex;
  flex-direction: column;
  max-width: 370px;
  color: #fff;
}

.free .btn {
  width: 100%;
}

.free__title {
  margin-bottom: 10px;
  font-weight: 400;
  font-size: 25px;
  text-transform: uppercase;
}

.free__desc {
  margin-bottom: 20px;
  font-size: 14px;
}

.free__form {
  display: flex;
  flex-direction: column;
  margin: 0 0 7px;
}

.free__input {
  height: 45px;
  margin: 0 0 6px;
  padding-left: 23px;
  font-size: 14px;
  font-family: "PT Sans", sans-serif;
  border: transparent;
  border-radius: 50px;
}

.free__input::placeholder {
  color: #000;
}

.free__span {
  margin: 0 0 23px;
  font-size: 12px;
  line-height: 1.3;
}

.free__span a {
  color: #fac577;
}

.header-main {
  position: relative;
  z-index: 0;
  padding-bottom: 207px;
}

.header-main .title {
  position: relative;
  z-index: 1;
  margin-bottom: 70px;
}

.header-main .title svg {
  position: absolute;
  z-index: -1;
  top: -57.8%;
  left: -47%;
  width: 328px;
  user-select: none;
  fill: #2b5998;
}

.header-main__img {
  position: absolute;
  top: -28%;
  right: -15.9%;
  user-select: none;
  pointer-events: none;
}

.logo-mini {
  position: absolute;
  z-index: -1;
  top: 15px;
  left: 50%;
  width: 230px;
  transform: translate(-50%);
  user-select: none;
  pointer-events: none;
  fill: #e5e5e5;
}

.logo {
  display: table;
  z-index: 10;
  width: min-content;
  padding: 5px;
  color: #fff;
  text-align: center;
  fill: #fac577;
}

.logo:hover {
  color: #fac577;
  filter: drop-shadow(8px 8px 8px #000);
  fill: #a07539;
}

.logo__brand {
  margin-bottom: 10px;
}

.logo__name {
  margin-bottom: 6px;
}

.logo__desc {
  display: block;
  margin: 0 -5px -2px;
  font-size: 10px;
  font-family: CormorantGaramond, Courier, monospace;
  line-height: 1.4;
  letter-spacing: 0.21em;
  text-transform: uppercase;
}

.request {
  display: flex;
  justify-content: center;
  font-size: 14px;
}

.request__avatar {
  display: flex;
  flex: 0 0 53px;
  align-items: flex-end;
  justify-content: center;
  width: 53px;
  height: 53px;
  border-radius: 50%;
