.stuff__title {
  max-width: 660px;
  margin-bottom: 50px;
  font-weight: 400;
  font-size: 35px;
  line-height: 1.3;
  letter-spacing: 0.02em;
  text-transform: uppercase;
}

.stuff__list {
  max-width: 660px;
  margin-bottom: 68px;
  padding-left: 50px;
  font-size: 14px;
  font-family: "PT Sans", sans-serif;
  line-height: 1.5;
  list-style: none;
}

.stuff__list li {
  position: relative;
  margin-bottom: 11px;
}

.stuff__list li::before {
  content: "";
  display: inline-block;
  position: absolute;
  top: 0;
  left: -50px;
  width: 23px;
  height: 23px;
  background: #fac577 url("../img/icons/check.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: 50%;
  border-radius: 50%;
}

.no-webp .stuff {
  background: #2961ad url("../img/stuff/bg.png");
}

.trust {
  position: relative;
  padding: 33px 0 41px;
  color: #fff;
  background: linear-gradient(rgba(43, 89, 152, 0.65)), url("../img/trust/bg.webp");
  background-repeat: no-repeat;
  background-size: cover;
  background-blend-mode: normal;
}

.trust__title {
  margin-bottom: 37px;
}

.trust__row {
  display: flex;
  justify-content: space-around;
}

.trust__skill {
  flex: 1 1 100%;
  max-width: 250px;
  margin-bottom: 40px;
}

.trust__check {
  position: relative;
  display: block;
  width: 36px;
  height: 36px;
  margin: 0 auto 15px;
  background-color: #fac577;
  border: 2px solid #fff;
  border-radius: 50%;
}

.trust__check::before {
  position: absolute;
  width: 100%;
  height: 100%;
  background-image: url("../img/icons/check.svg");
  background-repeat: no-repeat;
  background-position: center;
  content: '';
}

.trust__name {
  margin-bottom: 0;
  font-size: 18px;
  font-family: 'PT Serif', serif;
  line-height: 1.17;
  text-align: center;
}

.trust .btn {
  display: block;
  width: 200px;
  margin: 0 auto;
}

.no-webp .trust {
  background: linear-gradient(rgba(43, 89, 152, 0.65)), url("../img/trust/bg.jpg");
}

.footer {
  padding: 22px 0 21px;
  color: #fff;
  font-size: 18px;
  background: linear-gradient(90deg, rgba(0, 0, 0, 0) 30%, black 50%), url("../img/footer/bg.webp");
  background-color: rgba(0, 0, 0, 0.5);
  background-repeat: no-repeat;
  background-position: left;
  background-blend-mode: multiply;
}

.footer a:not(.logo) {
  color: #fff;
}

.footer a:not(.logo):hover,
.footer a:not(.logo):focus,
.footer a:not(.logo):active {
  color: #fac577;
}

.footer .logo {
  grid-area: logo;
  align-self: center;
  justify-self: start;
}

.footer__grid {
  display: grid;
  grid-template-areas: 'logo serv serv nav' 'logo list1 list2 nav' 'logo poli poli copy';
  grid-template-rows: 28px auto auto;
  grid-template-columns: 32% 16% 27% auto;
}

.footer__services {
  grid-area: serv;
  font-size: 18px;
  border-bottom: 1px solid #fff;
}

.footer__services h4 {
  margin: 0;
  font-weight: 400;
}

.footer__list {
  padding-top: 12px;
  font-size: 14px;
  font-family: 'PT Sans', sans-serif;
}

.footer__list.list_1 {
  grid-area: list1;
}

.footer__list.list_2 {
  grid-area: list2;
  justify-self: flex-end;
}

.footer__nav {
  display: flex;
  flex-direction: column;
  grid-area: nav;
  justify-self: end;
  font-size: 18px;
  line-height: 1.6;
}

.footer__nav a:first-child {
  display: none;
}

.footer__bottom {
  align-self: flex-end;
  padding-top: 18px;
  font-size: 14px;
  font-family: 'PT Sans', sans-serif;
}

.footer a.footer__bottom {
  grid-area: poli;
  text-decoration: underline;
}

.footer p.footer__bottom {
  grid-area: copy;
  justify-self: end;
  margin: 0;
}

.no-webp .footer::before {
  background-image: url("../img/footer/bg.jpg");
}

.header {
  overflow: hidden;
  padding: 10px 0 0;
  color: #fff;
  background-color: rgba(0, 59, 138, 0.9);
  background-image: url("../img/header/bg.webp");
  background-repeat: no-repeat;
  background-size: cover;
  background-blend-mode: color-burn;
}

.header .logo {
  margin-right: 60px;
}

.header__row {
  width: 100%;
}

.header__top {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
  padding: 3px 0 10px;
  font-size: 14px;
  border-bottom: 1px solid #fff;
}

.header__top > div {
  display: flex;
  align-items: center;
}

.header__top > div img {
  margin-right: 20px;
  margin-bottom: 5px;
}

.header__top > div img[alt="Mail"] {
  margin-right: 12px;
}

.header__top > div img[alt="Phone"] {
  margin-right: 14px;
}

.header__top > div p {
  margin: 0;
  font-family: "PT Serif", serif;
  line-height: 1.35;
}

.header__top .btn {
  width: 185px;
  height: 35px;
  margin-left: 20px;
}

.header__nav {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  padding: 32px 0 0;
  margin-bottom: 70px;
  font-size: 16px;
}

.header__nav a {
  z-index: 10;
  margin: 0 -20px;
  padding: 6px 20px 7px;
  color: #fff;
  border-radius: 50px;
}

.header__nav a:hover,
.header__nav a:focus,
.header__nav a:active {
  color: #000;
  background-color: #fac577;
}

.no-webp .header {
  background-image: url("../img/header/bg.jpg");
}